* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.app {
  margin: 15px;
}

.page {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.page-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.video-content {}

.video {
  margin-bottom: 16px;
}

.playlist {
  margin-left: 24px;
  width: 400px;
}

.comments {
  width: 640px;
}

.searchbar {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 24px;
  width: 1064px;
}

.searchbar__input {
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 4px 0 0 4px;
  color: #495057;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 16px;
  line-height: 1.5;
  padding: 6px 12px;
}

.searchbar__submit {
  background-color: #868e96;
  border: 1px solid #868e96;
  border-radius: 0 4px 4px 0;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  margin-left: -1px;
  padding: 6px 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.searchbar__submit:hover {
  background-color: #727b84;
  border-color: #6c757d;
}

/* Playlist item */
.playlist-item {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 8px;
}

.playlist-item__preview {
  height: 94px;
  margin-right: 8px;
}

.playlist-item__title {
  font-size: 14px;
  font-weight: 500;
}

.playlist-item__channel-title {
  color: rgba(17, 17, 17, 0.6);
  font-size: 13px;
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

/* Comments */
.comment {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 16px;
}

.comment__text {
  font-size: 14px;
}

.comment__replies-btn {
  background: transparent;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0.7em 0.57em;
  text-align: center;
}

.comment__replies-btn:hover {
  font-weight: bold;
}

.comment__replies-btn i {
  padding-left: 4px;
}

.author__avatar {
  border-radius: 50%;
  height: 28px;
  margin-right: 16px;
}

.author__title {
  color: rgb(17, 17, 17);
  cursor: pointer;
  display: inline-block;
  font-size: 13px;
  font-weight: bold;
  line-height: 1.5;
  margin-right: 8px;
  margin-bottom: 8px;
  text-decoration: none;
}

.next-page-btn {
  background-color: rgb(238, 238, 238);
  border: 0;
  cursor: pointer;
  padding: 8px;
  width: 100%;
}



